<template>
    <div class="page-wrapper">
            <div class="clients-video isSoon"v-if="clients.isSoon">
                <h2> Próximamente... </h2>
            </div>
             <div v-if="!clients.isSoon">
                <div i class="clients-video hide-on-med-and-down">
                    <img class="client-image slide-top" :src="clients.logoPath" alt="">
                </div>
                <div class="video-list-container hide-on-med-and-down">
                    <ul class="video-list">
                        <li v-for="(index, keyItem ) in clients.list" class="list"  >
                            <a href="#" @click.prevent="showVideo(keyItem)" class="btn-video-link" :data-video-link="keyItem"  > {{ index.name }} </a>
                        </li>
                    </ul>
                </div>
                <div class="embed-video-container hide-on-med-and-down">
                    <div class="embed-video video-1" :data-embed="keyItem" v-for="(index, keyItem ) in clients.list" v-show="keyItem == 0">
                        <iframe :src="index.video" frameborder="0" allow="autoplay; fullscreen" allowfullscreen v-if="index.video"></iframe>
                        <div class="row" v-show="index.imageList">
                            <div class="imageList-carousel">
                            <carousel :perPage="1">
                                <slide v-for="i in index.imageList">
                                    <div class="image-slide" :style="{'background' : 'url('+i.path+')', backgroundSize: 'contain', backgroundRepeat : 'no-repeat',   backgroundPosition: 'center', width: '100%', }"></div>
                                </slide>
                            </carousel>
                            </div>
                        </div>
                        <div class="row" v-show="index.videoList">
                            <div class="imageList-carousel">
                                <carousel :perPage="1">
                                    <slide v-for="i in index.videoList">
                                        <video  class="image-slide" muted="true" autoplay="true" controls>
                                            <source :src="i.path" type="video/mp4">
                                        </video>
                                    </slide>
                                </carousel>
                            </div>
                        </div>
                      
                    </div>
                </div>
                <div class="show-on-medium-and-down hide-on-med-and-up clients-mobile">
                    <img class="client-image-mobile slide-top" :src="clients.logoPath"  width="150" alt="">
                    <div class="card-list-mobile">
                        <div class="card mobile"  v-for="index in clients.list" >
                            <div class="card-image waves-effect waves-block waves-light">
                                <div class="embed-video-container-mobile">
                                    <div class="embed-video-mobile">
                                        <div style="padding: 100px 0px;">
                                            <iframe v-if="index.video" :src="index.video" frameborder="0" allow="autoplay; fullscreen" allowfullscreen ></iframe>
                                        </div>
                                        <div v-for="indexImage in index.imageList"  v-if="index.imageList" style="height:200px; padding:20px;">
                                            <div :style="{'background' : 'url('+indexImage.path+')', backgroundSize: 'contain', backgroundRepeat : 'no-repeat',   backgroundPosition: 'center', height:'100%' }"/>
                                        </div> 
                                        <div v-for="indexVideo in index.videoList"  v-if="index.videoList" style="padding:10px;">
                                            <video  muted="true" autoplay="true" controls style="width:100%">
                                                <source :src="indexVideo.path" type="video/mp4">
                                            </video>
                                        </div> 
                                    </div>
                                </div>
                            </div>
                            <div class="card-content">
                                <span class="card-title activator grey-text text-darken-4"> {{ index.name }}</span>
                            </div>
                        </div>
                    </div>
                </div>
             </div>
        </div>
</template>
<script>
import $ from 'jquery'
import { Carousel, Slide } from 'vue-carousel';

export default {
    
    props : [ 'clients' ],
    data() { 
        return {
            current: 0,
            direction: 1,
            transitionName: "fade",
            show: true,
        }
    },
    components: {
        Carousel,
        Slide
    },
    methods: {
        slide(dir, arrLength) {
            console.log( arrLength)
        this.direction = dir;
        dir === 1
            ? (this.transitionName = "slide-next")
            : (this.transitionName = "slide-prev");
        var len = arrLength;
        this.current = (this.current + dir % len + len) % len;
        },
        showVideo( index ){
            this.current = 0
            $(this).css({ 'font-weight': 900 })
            $('.btn-video-link[data-video-link!="'+index+'"]').css({ 'font-weight': 400 })
            $('.embed-video[data-embed="'+index+'"]').fadeIn()
            $('.embed-video[data-embed!="'+index+'"]').fadeOut()
        }
    },
    created(){
        
        setTimeout( function(){
            let elements = document.querySelectorAll('.video-list-container');
            for (var i=0; i<elements.length; i++) {
                elements[i].classList.add('reveal');
            }
        },1000)
    },
}
</script>
